// src/pages/NodeDetailPage.jsx
import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import { Line } from 'react-chartjs-2';
import { Chart, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, TimeScale } from 'chart.js';
import 'chartjs-adapter-date-fns';

Chart.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, TimeScale);

function NodeDetailPage() {
  const { nodeId } = useParams();
  const [chartData, setChartData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [stats, setStats] = useState(null);

  useEffect(() => {
    const fetchHistory = async () => {
      try {
        // Add proper headers to fetch request
        const url = `/api/data/histor/${nodeId}`;
        console.log('Requesting URL:', url);
        console.log('NodeId:', nodeId);
        
        const response = await fetch(url, {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json'
          }
        });
        console.log('Response status:', response.status);
        console.log('Response headers:', Object.fromEntries(response.headers.entries()));
        
        if (!response.ok) {
          console.log(' Response not OK');
          console.log('Status:', response.status);
          console.log('Status Text:', response.statusText);
          console.log('Response Headers:', Object.fromEntries(response.headers.entries()));
          
          const errorText = await response.text();
          console.log(' Error response body:', errorText);
          throw new Error(`HTTP ${response.status}: ${response.statusText}`);
        }
        
        const contentType = response.headers.get('content-type');
        console.log(' Content-Type:', contentType);
        
        if (!contentType || !contentType.includes('application/json')) {
          const responseText = await response.text();
          console.log(' Non-JSON response:', responseText);
          throw new Error('Server returned non-JSON response');
        }
        
        const data = await response.json();
        console.log(' Successfully parsed JSON data:', data);
        console.log(' Data type:', typeof data);
        console.log(' Data keys:', Object.keys(data));
        
        // data is a single MongoDB document object
        if (!data || typeof data !== 'object') {
          setError('No data available for this node');
          return;
        }

        // data is the document itself
        const document = data;
        
        if (!document.current || !Array.isArray(document.current)) {
          setError('Invalid data format - current readings not found');
          return;
        }

        const currentReadings = document.current;
        
        // Calculate statistics
        const average = currentReadings.reduce((sum, val) => sum + val, 0) / currentReadings.length;
        const max = Math.max(...currentReadings);
        const min = Math.min(...currentReadings);
        
        setStats({
          nodeId: document.nodeId,
          totalReadings: currentReadings.length,
          average: parseFloat(average.toFixed(3)),
          max: parseFloat(max.toFixed(3)),
          min: parseFloat(min.toFixed(3)),
          createdAt: new Date(document.createdAt),
          updatedAt: new Date(document.updatedAt)
        });

        // Create chart data - plot all current readings over reading index
        const formattedData = {
          labels: currentReadings.map((_, index) => index + 1), // Reading number (1, 2, 3, ...)
          datasets: [{
            label: 'Current (A)',
            data: currentReadings,
            borderColor: '#3b82f6',
            backgroundColor: 'rgba(59, 130, 246, 0.1)',
            fill: true,
            tension: 0.1,
            pointRadius: 0, // Hide individual points for better performance
            pointHoverRadius: 3,
            borderWidth: 1.5,
          }],
        };
        
        setChartData(formattedData);
      } catch (err) {
        console.error(' Complete fetch error:', err);
        console.error(' Error name:', err.name);
        console.error(' Error message:', err.message);
        console.error(' Error stack:', err.stack);
        
        if (err.name === 'SyntaxError') {
          console.error(' JSON Parsing Error - Server returned invalid JSON');
          setError('Server returned invalid JSON. Check server logs.');
        } else if (err.message.includes('fetch')) {
          console.error(' Network Error');
          setError(`Network error: ${err.message}`);
        } else {
          setError(`Failed to fetch data: ${err.message}`);
        }
      } finally {
        setLoading(false);
      }
    };

    fetchHistory();
  }, [nodeId]);

  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: { 
        position: 'top', 
        labels: { color: '#f9fafb' } 
      },
      title: { 
        display: true, 
        text: `Current Readings - Node ${nodeId}`, 
        color: '#f9fafb', 
        font: { size: 18 } 
      },
      tooltip: {
        backgroundColor: 'rgba(31, 41, 55, 0.9)',
        titleColor: '#f9fafb',
        bodyColor: '#f9fafb',
        borderColor: '#6b7280',
        borderWidth: 1,
        callbacks: {
          title: function(context) {
            return `Reading #${context[0].label}`;
          },
          label: function(context) {
            return `Current: ${context.parsed.y.toFixed(3)}A`;
          }
        }
      }
    },
    scales: {
      x: { 
        ticks: { 
          color: '#9ca3af',
          maxTicksLimit: 10 // Limit number of x-axis labels
        }, 
        grid: { color: 'rgba(156, 163, 175, 0.1)' },
        title: {
          display: true,
          text: 'Reading Number',
          color: '#f9fafb'
        }
      },
      y: { 
        ticks: { color: '#9ca3af' }, 
        grid: { color: 'rgba(156, 163, 175, 0.1)' },
        title: {
          display: true,
          text: 'Current (A)',
          color: '#f9fafb'
        }
      },
    },
    interaction: {
      intersect: false,
      mode: 'index'
    },
    elements: {
      line: {
        tension: 0.1
      }
    }
  };

  if (loading) {
    return (
      <div style={{ padding: '2rem', textAlign: 'center' }}>
        <p className="loading-message" style={{ color: '#9ca3af', fontSize: '1.1rem' }}>
          Loading chart data...
        </p>
      </div>
    );
  }

  if (error) {
    return (
      <div style={{ padding: '2rem' }}>
        <Link to="/dashboard" style={{ color: '#3b82f6', marginBottom: '2rem', display: 'inline-block' }}>
          &larr; Back to Dashboard
        </Link>
        <div className="error-message" style={{ 
          backgroundColor: '#fee2e2', 
          color: '#dc2626', 
          padding: '1rem', 
          borderRadius: '8px',
          border: '1px solid #fecaca'
        }}>
          <h3>Error Loading Data</h3>
          <p>{error}</p>
        </div>
      </div>
    );
  }

  return (
    <div style={{ padding: '2rem', backgroundColor: '#111827', minHeight: '100vh', color: '#f9fafb' }}>
      <Link to="/dashboard" style={{ 
        color: '#3b82f6', 
        marginBottom: '2rem', 
        display: 'inline-block',
        textDecoration: 'none',
        fontSize: '1rem'
      }}>
        &larr; Back to Dashboard
      </Link>
      
      <h1 style={{ marginBottom: '1rem', color: '#f9fafb' }}>
        Current Readings for Node: {nodeId}
      </h1>

      {/* Statistics Display */}
      {stats && (
        <div style={{ 
          marginBottom: '2rem', 
          display: 'grid', 
          gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))', 
          gap: '1rem' 
        }}>
          <div style={{ 
            backgroundColor: '#1f2937', 
            padding: '1rem', 
            borderRadius: '8px',
            border: '1px solid #374151'
          }}>
            <h3 style={{ margin: '0 0 0.5rem 0', color: '#3b82f6' }}>Reading Statistics</h3>
            <p style={{ margin: '0.25rem 0' }}>Average: <strong>{stats.average}A</strong></p>
            <p style={{ margin: '0.25rem 0' }}>Maximum: <strong>{stats.max}A</strong></p>
            <p style={{ margin: '0.25rem 0' }}>Minimum: <strong>{stats.min}A</strong></p>
            <p style={{ margin: '0.25rem 0' }}>Range: <strong>{(stats.max - stats.min).toFixed(3)}A</strong></p>
          </div>
          
          <div style={{ 
            backgroundColor: '#1f2937', 
            padding: '1rem', 
            borderRadius: '8px',
            border: '1px solid #374151'
          }}>
            <h3 style={{ margin: '0 0 0.5rem 0', color: '#10b981' }}>Data Information</h3>
            <p style={{ margin: '0.25rem 0' }}>Total Readings: <strong>{stats.totalReadings.toLocaleString()}</strong></p>
            <p style={{ margin: '0.25rem 0' }}>Node ID: <strong>{stats.nodeId}</strong></p>
            <p style={{ margin: '0.25rem 0' }}>Created: <strong>{stats.createdAt.toLocaleString()}</strong></p>
            <p style={{ margin: '0.25rem 0' }}>Updated: <strong>{stats.updatedAt.toLocaleString()}</strong></p>
          </div>
        </div>
      )}

      <div style={{ 
        height: '60vh', 
        backgroundColor: '#1f2937', 
        padding: '1rem', 
        borderRadius: '12px',
        border: '1px solid #374151'
      }}>
        {chartData ? (
          <Line options={chartOptions} data={chartData} />
        ) : (
          <p style={{ textAlign: 'center', color: '#9ca3af', marginTop: '2rem' }}>
            No data to display.
          </p>
        )}
      </div>

      {/* Additional Info */}
      <div style={{ 
        marginTop: '1rem', 
        padding: '1rem', 
        backgroundColor: '#1f2937', 
        borderRadius: '8px',
        border: '1px solid #374151'
      }}>
        <p style={{ margin: 0, color: '#9ca3af', fontSize: '0.9rem' }}>
          <strong>Note:</strong> This chart shows {stats?.totalReadings || 0} individual current readings 
          from the sensor array. Each point represents a single measurement.
        </p>
      </div>
    </div>
  );
}

export default NodeDetailPage;